.home {
    padding: 20px;
  }
  
  .add-task input,
  .add-task button,
  .add-course input,
  .add-course button {
    margin: 5px;
  }
  
  .assignment-table,
  .courses-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
  }
  
  .assignment-table th,
  .courses-table th,
  .assignment-table td,
  .courses-table td {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  .assignment-table th,
  .courses-table th {
    background-color: #f4f4f4;
  }
  
  .add-course {
    margin-top: 20px;
  }
  
  .add-course input {
    padding: 8px;
    width: 200px;
  }
  
  .add-course button {
    padding: 8px;
    background-color: #007bff;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  .add-course button:hover {
    background-color: #0056b3;
  }
  
  .dashboard {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
  }
  
  .calendar-section, .deadline-widget {
    width: 48%;
  }
  
  .deadline-widget {
    background: #f8f9fa;
    padding: 15px;
    border-radius: 8px;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.1);
  }
  
  .deadline-widget ul {
    list-style: none;
    padding: 0;
  }
  
  .deadline-widget li {
    padding: 8px 0;
    display: flex;
    justify-content: space-between;
    font-size: 16px;
  }
  
  .days-left {
    font-weight: bold;
    padding: 4px 8px;
    border-radius: 4px;
    background: #28a745;
    color: white;
  }
  
  .days-left.urgent {
    background: #dc3545;
  }
  
  .completed-task {
    background-color: #d4edda !important; /* Light green for completed tasks */
    text-decoration: line-through;
  }
  
  .deadline-widget li {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px 0;
  }
  
  .done-button {
    background: #28a745;
    color: white;
    border: none;
    padding: 5px 10px;
    cursor: pointer;
    border-radius: 5px;
  }
  
  .done-button:disabled {
    background: #ccc;
    cursor: not-allowed;
  }
  
  .days-left.urgent {
    background: #dc3545;
    padding: 4px 8px;
    border-radius: 4px;
    color: white;
  }
  
  /* Highlight dates with assignments */
.highlight-date {
    background-color: #f07777 !important;  /* Light red highlight */
    border-radius: 0%;
  }
  
  /* Style for task names inside the calendar */
  .calendar-tasks {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 10px;
  
    padding: 2px;
    border-radius: 4px;
    text-align: center;
  }
  
  .calendar-task {
    font-size: 10px;
    color: #333;
   
    padding: 2px;
    border-radius: 3px;
    margin: 1px 0;
    text-align: center;
  }
  